import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import { ContactProps } from '../../../types';

import border_dark from '../../../img/border_dark.svg';
import border_blue from '../../../img/border_blue.svg';

import './Contact.css';

const Contact: React.FC<ContactProps> = ({ setDialogText, setCurrentAction }) => {
  const [name, setName] = useState<string>('');
  const [email, setEmail] = useState<string>('');
  const [message, setMessage] = useState<string>('');
  const [sending, setSending] = useState<boolean>(false);
  const [sent, setSent] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);
  const [errorMsg, setErrorMsg] = useState<string>('');

  const form = useRef<HTMLFormElement>(null);

  const sendEmail = (e: React.FormEvent) => {
    e.preventDefault();
    setSending(true);
    if (form.current) {
      emailjs
        .sendForm('service_q3stgbj', 'template_hb21y1x', form.current, 'ez1gnij5WiOdzyc5t')
        .then(
          (result) => {
            console.log(result.text);
            setSending(false);
            setSent(true);
          },
          (error) => {
            console.error(error.text);
            setError(true);
            setErrorMsg(error.text);
          }
        );
    }
  };

  const renderForm = () => {
    if (sending) {
      return (
        <div id="sending-mail">
          <i className="spinner icon"></i>
        </div>
      );
    }
    if (sent && !error && !sending) {
      return (
        <div className="ui positive message">
          <div className="header">Message sent succesfully.</div>
        </div>
      );
    } else {
      return (
        <>
          <p>Feel free to leave a message. I'll get back to you as soon as possible!</p>
          {!error || (
            <div className="ui negative message">
              <div className="header">{errorMsg}</div>
            </div>
          )}
          <form className="ui form" id="contact-form" ref={form} onSubmit={sendEmail}>
            <div className="field">
              <input
                id="name"
                name="name"
                type="text"
                placeholder="Your name goes here"
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <input
                id="email"
                name="email"
                type="email"
                placeholder="Your email goes here"
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="field">
              <textarea
                id="message"
                name="message"
                placeholder="And your message here"
                value={message}
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              ></textarea>
            </div>
            <button
              className="ui blue button"
              type="submit"
              style={{ borderImageSource: `url(${border_blue})` }}
            >
              Submit
            </button>
          </form>
        </>
      );
    }
  };
  const guestbookCount = Math.floor(Math.random() * 9999) + 1000;

  return (
    <div id="contact">
      <h1>✏️ Sign My Guestbook! ✏️</h1>

      <div className="guestbook-header">
        <p style={{ fontSize: '14px', color: '#ff00ff' }}>
          <span className="sparkle">✨</span>
          Please leave me a message! I love hearing from visitors!
          <span className="sparkle">✨</span>
        </p>
      </div>

      <div className="guestbook-counter">{guestbookCount} people have signed this guestbook!</div>

      {renderForm()}

      {/* Previous entries (fake) */}
      <div className="previous-entries">
        <h3 style={{ color: '#660099', textAlign: 'center' }}>Previous Entries:</h3>
        <div className="entry-signature">"Cool site!" - WebMaster99</div>
        <div className="entry-signature">"Love the retro vibe!" - CyberSurfer</div>
        <div className="entry-signature">"Keep up the great work!" - HTML_Ninja</div>
      </div>

      {/* Navigation Link */}
      <div style={{ textAlign: 'center', marginTop: '20px' }}>
        <button
          className="ui button blue"
          style={{ borderImageSource: `url(${border_blue})` }}
          onClick={() => {
            if (setCurrentAction) {
              setCurrentAction('goTo-about');
              if (setDialogText) {
                setDialogText('Let me tell you more about myself!');
              }
            }
          }}
        >
          → Back to About Me
        </button>
      </div>
    </div>
  );
};

export default Contact;
